.info-main {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  color: #FFFFFF;
  font-size: 16px;
  padding: 0 32px;
  margin-top: 50px;

  .title {
    font-size: 32px;

    a:hover {
      text-decoration: underline;
    }
  }

  .recharts-tooltip-wrapper {
    visibility: visible !important;
  }

  .charts {
    display: flex;
    margin: 0 auto;
    margin-top: 40px;
    justify-content: space-between;
    display: flex;
    max-width: 1440px;

    .graph {
      width: 49%;
      max-width: 680px;
      background: rgba(85, 119, 253, 0.1);
      border: 1px solid #203B60;
      height: 440px;
      display: flex;
      align-items: flex-end;
    }

    .graph.volume24 {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }

    .netValue,
    .tokens {
      display: flex;
      flex-direction: column;
      padding-top: 28px;
      height: 480px;
    }


  }

  .list-by-chain {
    margin: 0 auto;
    margin-bottom: 112px;
    display: flex;
    flex-direction: column;
    max-width: 1440px;

    .table {
      max-width: 1440px;
      font-size: 14px;

      .rc-table-cell:first-child {
        padding-left: 24px;
      }

      .rc-table-cell {
        min-width: 100px;
        text-align: left;
      }

      .rc-table-cell.base-token {
        span {
          height: auto !important;
          line-height: inherit !important;
        }
      }
    }
  }
}

@media only screen and (max-width: 760px) {
  .info-main {
    padding: 0 24px;
    margin-top: 24px;

    .title {
      font-size: 20px;
    }

    .charts {
      display: flex;
      margin-top: 0px;
      justify-content: space-between;
      display: flex;
      flex-direction: column;
      max-width: 1440px;

      .graph {
        width: 100%;
        max-width: 680px;
        margin-top: 24px;
      }
    }

    .list-by-chain {
      margin-bottom: 112px;

      .table {
        .rc-table-cell:first-child {
          padding-left: 24px;
        }

        .rc-table-cell {
          min-width: 100px;
          text-align: left;
        }
      }
    }
  }
}